:root {
    --off-black: #333333;
  }
  
  .App-header {
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: black;
    width: auto;
    background: rgba( 255, 255, 255, 0.25 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    margin: 5%;
    padding: 20px;
    z-index: z-indexundefined 1;
  }
  
  #header-h1 {
    font-size: 5vw;
    color: var(--off-black);
  }
  
  .header-p {
    color: #4b4b4b;
    font-weight: 549;
    
  }
  
  #header-h1, .header-p {
    padding: 15px;
  }
  
 
  @media screen and (max-width: 769px) {
    #header-h1 {
      font-size: 6vh;
    }
    .header-p {
      font-size: 2.2vh
    }
  }